/*=============== DESKTOP VIEW ======================= */
body {
	max-width: 100%;
    overflow-x: hidden;
}
/* Header */
#header {
	display: -moz-flex;
	display: -ms-flex;
	display: -ms-flexbox;
	display: flex;
	-moz-flex-direction: column;
	-ms-flex-direction: column;
	flex-direction: column;
	-moz-align-items: -moz-flex-end;
	-ms-align-items: -ms-flex-end;
	-ms-flex-align: end;
	    align-items: flex-end;
	-moz-justify-content: space-between;
	-ms-justify-content: space-between;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background-color: #1f1815;
	background-attachment: scroll,								fixed;
	background-image: url("./bright.jpg"), url("./bright.jpg");
	background-position: top left,							top left;
	background-repeat: repeat,								no-repeat;
	background-size: auto,								auto 100%;
	color: rgba(0, 0, 0, 0.5);
	height: 100%;
	left: 0;

	/*Padding for the left hand elements*/
	padding: 6em 4em;

	position: fixed;
	text-align: right;
	top: 0;
	width: 30%;
}

#header h2, #header h3, #header h4, #header h5, #header h6 {
	color: #000000;
}

#header h1 {
	color: rgba(0, 0, 0, 0.5);
	/*font-size: 1.35em;*/
	line-height: 1.75em;
	margin: 0;
}

/* Main */
#main {
margin-left: 35%;
max-width: 54em;
/*put 8em first to add padding to the top */
padding: 2em 2em 4em 4em;
width: calc(100% - 35%);
}

#main > section {			
	margin: 4em 0 0 0;
	/*Padding for the right hand stuff (Not the top)*/
	padding: 0em 2em 0 2em;
}

	#main > section:first-child {
		margin-top: 0;
		padding-top: 0;
		margin-right: 43px;
	}

	/*PUT THE MOBILE RENDITION HERE */	

	/*For the Headshot*/
	.circular--square {
		border-top-left-radius: 15% 20%;
		border-top-right-radius: 20% 20%;
		border-bottom-right-radius: 20% 20%;
		border-bottom-left-radius: 10% 20%;
		float: left;
		padding-right: 20px;
		}

		.tab{
			margin-top: 350px;
		}

a {
	color:inherit;
	text-decoration: none;
	}

	/*html, body {margin: 0; height: 100%; overflow: hidden}*/
	.huerotate { -webkit-filter: hue-rotate(180deg); filter: hue-rotate(180deg); }
h1 { 
	color: #111; 
	font-family: 'Helvetica Neue', sans-serif; 
	font-size: 40px; 
	font-weight: bold; 
	letter-spacing: -1px; 
	line-height: 1; 
	text-align: left;
}

h1.FirstName { 
	font-size: 40px; 
}

h2 { 
	color: rgba(1, 121, 73, 0.959); 
	font-family: 'Open Sans', sans-serif; 
	font-size: 20px; 
	font-weight: 300; 
	line-height: 1; 
	text-align: left; 
}


p { 
	color: #685206; 
	font-family: 'Helvetica Neue', sans-serif; 
	font-size: 14px; 
	line-height: 1; 
	text-align: justify; 
	text-justify: inter-word; 
}

p.BodyInfo {
	font-size: 20px;
	padding: 1%;
	line-height: 1.5;
}

a:hover {
	color: rgba(255, 166, 0, 0.856);
}

.para {
	-webkit-text-decoration-style: dotted;
	        text-decoration-style: dotted;
}


.miniLinks{
	padding-right: 20px;
	padding-top: 10px;
	float: left;
}

.mobileOnly {
	clear: both;
	visibility: hidden;
	margin: 0px;
}

.publications-margin {
	margin-right: 49px;
}

/*=============== MOBILE VIEW (IPHONE 6/7/8) ======================= */
@media screen and (max-width: 736px){
	ul, menu, dir {
		display: block;
		list-style-type: disc;
		-webkit-margin-before: 1em;
		-webkit-margin-after: 1em;
		-webkit-margin-start: 0px;
		-webkit-margin-end: 0px;
		-webkit-padding-start: 40px;
		width: 143%;
		padding: 0em 0em 0em 0em;
	}

	/* Top hr that hides on desktop view */
	.mobileOnly {
		visibility: visible;
		margin-top: -20px;
		margin-bottom: 30px;
	}
	
	hr{
		width: 315px;
	}
	.tab{
		margin-top: 100px;
		font-size: 13px;
	}
	.circular--square{
		width: 100%;
	}
	html {
		display: block;
	}
	p.BodyInfo{
		font-size: 15px;
		line-height: 1.5;
		width: 84vw;
		padding: 0em 0em 0em 0em;
	}
	.publications-margin{
		width: 56vw;
	}

	#header {
		display: block;
		position: absolute;
		top: 0;
		text-align: center;
		width: 74%;
		left: 0;
		padding: 3em 3em 3em 3em;
		height: 50%;
		background-size: auto, cover;
	}

		#header h1{
			font-size: 30px;
			text-align: center;

		}

		#header h2 {
			font-size: 15px;
			text-align: center;
		}


		img.miniLinks {
			margin-right: 15px;
			padding-right: 0px;
			padding-top: 0px;
			float: none; 
		}


	#main{
		margin-left: 0%;
		max-width: 14em;
		padding: 1em 1em 1em 1em;
		width: 100%;
		display: block; /*Might need to remove */

	}

		#main > section {
			margin: 0em 0em 0em 0em;
			padding: 0em 0em 0em 0em;
			
		}

		#main > section:first-child{
			margin-top: 10px;
			padding-top: 500px;
		}

}		

		/*Change icons size here*/
.social-icons .fa {
	font-size: 1.8em;
}
/*Change icons circle size and color here*/
.social-icons .fa {
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	color: #FFF;
	color: rgba(255, 255, 255, 0.8);
	-webkit-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.social-icons.icon-circle .fa{ 
	border-radius: 50%;
}
.social-icons.icon-rounded .fa{
	border-radius:5px;
}
.social-icons.icon-flat .fa{
	border-radius: 0;
}

.social-icons .fa:hover, .social-icons .fa:active {
	color: #FFF;
	-webkit-box-shadow: 1px 1px 3px #333;
	box-shadow: 1px 1px 3px #333; 
}
.social-icons.icon-zoom .fa:hover, .social-icons.icon-zoom .fa:active { 
 	-webkit-transform: scale(1.1);
	-ms-transform: scale(1.1);
	transform: scale(1.1); 
}
.social-icons.icon-rotate .fa:hover, .social-icons.icon-rotate .fa:active { 
	-webkit-transform: scale(1.1) rotate(360deg);
	-ms-transform: scale(1.1) rotate(360deg);
	transform: scale(1.1) rotate(360deg);
}
 
